@import '../GlobalAnimation.scss';

$detach-transition: transform $fullpager-section-duration $hsa-cubic-bezier;

.ai-image-row {
    .col {

        figure img {
            width: 100%;
            height: 100%;
        }

        .figure-l {
            // z-index: -1;
        }

        .figure-r {
            // z-index: -2;
        }

        .ai-detach-l {
            transform: translateX(0%) translateY(-10%) rotateZ(-20deg) scale(1.1);
            transition: $detach-transition 600ms;
        }

        .ai-detach-r {
            transform: translateX(0%) translateY(-10%) rotateZ(20deg) scale(1.1);
            transition: $detach-transition 700ms;
        }

        .ai-detach-v {
            transform: translateY(-10%) scale(1.1);
            transition: $detach-transition 800ms;
        }

        @mixin ai-merge($value) {
            transform: translateX($value);
            transition: $detach-transition;
        }

        .ai-merge-l {
            @include ai-merge(100%)
        }

        .ai-merge-r {
            @include ai-merge(-100%)
        }

        .ai-merge-v {
            @include ai-merge(0%)
        }

        .ai-shrink-l {
            transform: translateX(30%) translateY(-10%) rotateZ(-11deg) scale(1.05);
            transition: transform $hsa-image-shrik-duration $hsa-cubic-bezier 60ms;
        
        }

        .ai-shrink-r {
            transform: translateX(-30%) translateY(-10%) rotateZ(11deg) scale(1.05);
            transition: transform $hsa-image-shrik-duration $hsa-cubic-bezier 0ms;
        
        }

        .ai-shrink-v {
            transform: translateX(0%) translateY(-10%) scale(1);
            transition: transform $hsa-image-shrik-duration $hsa-cubic-bezier 40ms;
        
        }
    }

    .center {
        z-index: 2;
    }
}
  
#ai-phone-front-outline {
    width: 100%;
    height: 100%;
}

#ai-phone-front-content {
    position: absolute;
    top:5%;
    left:5%;
    width: 90%;
    height: 90%;
    mix-blend-mode: darken;
}

#ai-phone-search-front {
    position: absolute;
}

#ai-phone-front-content-figure {
    width: 100%;
    height: 100%;
}

#macbook-outline {
    width: 100%;
    height: 100%;
}
.coe-cover-page-container{
    background-image: url(/img/home/coe-cover-page.png);
    background-size: cover;
    width: 74%;
    height: 82%;
    display: flex;
    top: 48%;
    left: 46%;
    z-index: 1;
    transform: translate(-46%, -50%);
}


@media only screen and (max-width: 1400px) {
    .coe-cover-page-container {
        width: 74%;
        left: 42%;
        transform: translate(-42%, -50%);
    }
  }

.scroll-part {
    background-position: 0% 0%;
    transition: background-position $hsa-image-shrik-duration $hsa-cubic-bezier 600ms;
}

.scroll-whole {
    background-position: 0% 100%;
    transition: background-position 5000ms cubic-bezier(.18,.02,.85,1);
}

.scroll-none {
    background-position: 0% 20%;
    transition: background-position $hsa-image-shrik-duration $hsa-cubic-bezier 0ms;
}